import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import DeclineModal from './DeclineModal';
import AcceptButton from './AcceptButton';
import AcceptForm from './AcceptForm';
import leafSource from './assets/leaf.jpg';

export default class QuestionForm extends React.Component {

    constructor(props) {

        super(props);

        this.handler = this.props.handler.bind(this);
    }

    render() {

        return (
            <Container>
                <Row id="mainRow">

                    <Col md="4">
                        <img src={leafSource} alt="Superbe feuille" id="mainRow-image" />
                    </Col>

                    <Col md="8" id="mainRow-question">
                        <h4>Complter ce formulaire de contact</h4>

                        <p id="speech">Ce formulaire est utile  notre base contact. Une fois enregistr, vous recevrez notre catalogue produit
                            ainsi que nos newsletters. Grce  votre engagement au sein de notre association vous participez  rendre
                            ce monde plus juste. Merci !</p>

                        <DeclineModal />

                        <AcceptButton handler={this.props.handler} />
                    </Col>
                </Row>

                <AcceptForm blurredBackground={this.props.blurredBackground} />

            </Container>
        );
    }
}


